import React, { useState } from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  FormGroup,
  Label,
  Input,
  Table,
  ModalFooter,
} from "reactstrap";
import "../styles.css";
import { AiOutlineClose } from "react-icons/ai";

const InstallmentsModal = ({
  isOpen,
  toggleModal,
  approvedAmount,
}) => {
  const [firstInstallmentDate, setFirstInstallmentDate] = useState("");

  // Calculate auto-populated values
  // const installmentAmt = approvedAmount / installmentCount;
  const [installmentCount, setInstallmentCount] = useState(4);
  const [requestAmt, setRequestAmt] = useState(10000);
  const [approvAmt, setApproveAmt] = useState(requestAmt);
  const [date, setDate] = useState(new Date());
  const [plan, setPlan] = useState("");
  const [schedule, setSchedule] = useState([]);

  const handleGenerateSchedule = () => {
    // Generate schedule based on input values
    const generatedSchedule = [];
    const disbursementPlanMonths = {
      monthly: 1,
      quarterly: 3,
      'half-yearly': 6,
      yearly: 12,
      'one shot': 0
    };
    const disbursementPlanMonthDifference = disbursementPlanMonths[plan];
    if (disbursementPlanMonthDifference === undefined) {
      // Invalid plan, do nothing
      return;
    }
    for (let i = 0; i < parseInt(installmentCount); i++) {
      const plannedDate = new Date(date);
      plannedDate.setMonth(plannedDate.getMonth() + (i * disbursementPlanMonthDifference));
      const installmentAmt = parseFloat(approvAmt) / parseInt(installmentCount);
      generatedSchedule.push({
        sequence: i + 1,
        plannedDate: plannedDate.toISOString().slice(0, 10), // Format as "YYYY-MM-DD"
        amount: installmentAmt.toFixed(2)
      });
    }
    setSchedule(generatedSchedule);
  };

  const handleDateChange = (index, value) => {
    const updatedSchedule = [...schedule];
    updatedSchedule[index].plannedDate = value;
    setSchedule(updatedSchedule);
  };

  const handleAmountChange = (index, value) => {
    const updatedSchedule = [...schedule];
    updatedSchedule[index].amount = value;
    setSchedule(updatedSchedule);
  };

  const handleConfirm = () => {
    // Handle confirmation logic here
  };


  const handleSave = () => {
    const totalAmount = schedule.reduce((total, item) => total + parseFloat(item.amount), 0);
    if (totalAmount > parseFloat(approvAmt)) {
      alert('Total amount exceeds the approved amount!');
    } else {
      alert('Schedule saved successfully!');
    }
  };


  const handleAddRow = () => {
    const newRow = { sequence: schedule.length + 1, plannedDate: '', amount: '' };
    setSchedule([...schedule, newRow]);
  };

  const handleRemoveRow = (index) => {
    const updatedSchedule = schedule.filter((item, i) => i !== index);
    setSchedule(updatedSchedule);
  };

  return (
    <Modal isOpen={isOpen} toggle={toggleModal}>
      <ModalHeader toggle={toggleModal}>Disbursement schedule</ModalHeader>
      <ModalBody>
        <FormGroup className="form-group-input">
          <h5 style={{ fontSize: 14, width: 300 }}>Requested amount</h5>
          <Input
            style={{ width: "92%" }}
            id="requestAmt"
            type="text"
            value={requestAmt?.toFixed(2)}
            disabled
          />
        </FormGroup>
        <FormGroup className="form-group-input">
          <h5 style={{ fontSize: 14, width: 300 }}>Approved amount</h5>
          <Input
            style={{ width: "92%" }}
            id="approvAmt"
            type="text"
            value={approvAmt}
            onChange={(e)=> setApproveAmt(e.target.value)}
          />
        </FormGroup>
        <FormGroup className="form-group-input">
          <h5 style={{ fontSize: 14, width: 300 }}>Disbursement start date</h5>
          <Input
            id="disbursementDate"
            type="date"
            value={date}
            style={{ width: "92%" }}
            onChange={(e) => setDate(e.target.value)}
          />
        </FormGroup>
        <FormGroup className="form-group-input">
          <h5 style={{ fontSize: 14, width: 300 }}>Disbursement plan</h5>
          <Input
            type="select"
            name="disbursement_schedule"
            value={plan}
            onChange={(e)=>setPlan(e.target.value)}
          >
            <option value="">Select</option>
            <option value="monthly">Monthly</option>
            <option value="quarterly">Quarterly</option>
            <option value="half-yearly">Half-Yearly</option>
            <option value="yearly">Yearly</option>
            <option value="one shot">One Shot</option>
          </Input>
        </FormGroup>
        <FormGroup className="form-group-input">
          <h5 style={{ fontSize: 14, width: 300 }}>No of installments</h5>
          <Input
            style={{ width: "92%" }}
            id="installmentCount"
            type="text"
            value={installmentCount}
            onChange={(e) => setInstallmentCount(e.target.value)}
          />
        </FormGroup>
       {!schedule?.length && ( <Button style={{float:'right'}} color="primary" onClick={handleGenerateSchedule}>
          Generate schedule
        </Button>)}
        {schedule.length > 0 && (
          <Table   style={{marginTop:10}}>
            <thead>
              <tr>
                <th></th>
                <th>Disbursement date</th>
                <th>Amount</th>
              </tr>
            </thead>
            <tbody>
              {schedule.map((item, index) => (
                <tr key={index}>
                  <td>{index+1}</td>
                  <td>
                    <Input
                      type="date"
                      style={{width:"80%"}}
                      value={item.plannedDate}
                      onChange={(e) => handleDateChange(index, e.target.value)}
                    />
                  </td>
                  <td style={{display:'flex',alignItems:'center'}}>
                    <Input
                      type="text"
                      style={{width:"80%"}}
                      value={item.amount}
                      onChange={(e) => handleAmountChange(index, e.target.value)}
                    />
                      <AiOutlineClose onClick={()=>handleRemoveRow(index)} style={{marginLeft:15,cursor:'pointer'}}/>
                  </td>
                  
                </tr>
              ))}
            </tbody>
          </Table>
        )}
          
          {schedule?.length >0&& (    <div onClick={handleAddRow} style={{color:'blue',cursor:'pointer'}}>+ Add Row</div>)}
      </ModalBody>
      <ModalFooter>
        <Button color="primary" onClick={handleSave} >Save</Button>{' '}
        <Button color="primary" >Approve</Button>{' '}
      </ModalFooter>
    </Modal>
  );
};

